import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { BrowserRouter as Router, Routes, Route, Link } from 'react-router-dom';
import './App.css';
import { Breadcrumb, Layout, Menu, Button } from 'antd';
import { GoogleLogin, useGoogleLogin } from '@react-oauth/google';

const { Header, Content, Footer } = Layout;

function App() {
	const login = useGoogleLogin({
		onSuccess: async (codeResponse) => {
			const { code } = codeResponse;
			console.log(codeResponse);
			await axios.post("http://localhost:5000/auth/google", {
				code,
			})
		},
		flow: "auth-code",
	});
	
	const [time, setTime] = useState(new Date());

	const timer = setInterval(() => {
		setTime(new Date());
	}, 1000);

	useEffect(() => {
		return () => {
			clearInterval(timer);
		};
	}, [timer]);

	return (
		<Layout className="layout">
			<h3> : {time.toLocaleTimeString()}</h3>
			<Button onClick={() => {
				login();
			}}
			>
				
			</Button>
			<GoogleLogin
				onSuccess={(credentialResponse) => {
					console.log(credentialResponse);
				}}
				onError={() => {
					console.log("Login Failed");
				}}
			/>
			<Router>
				<Header>
					<Menu
						theme="dark"
						mode="horizontal"
						defaultSelectedKeys={["1"]}
						items={[
							{
								key: 1,
								label: <Link to="/">Home</Link>,
							},
							{
								key: 2,
								label: <Link to="/resume">Resume</Link>,
							},
							{
								key: 3,
								label: <Link to="/portfolio">Portfolio</Link>,
							},
						]}
					/>
				</Header>
				<Content style={{ padding: "0 50px" }}>
					<Breadcrumb style={{ margin: "16px 0" }}>
						<Breadcrumb.Item>Home</Breadcrumb.Item>
						<Breadcrumb.Item>Resume</Breadcrumb.Item>
						<Breadcrumb.Item>Portfolio</Breadcrumb.Item>
					</Breadcrumb>
					<div className="site-layout-content">
						<Routes>
							<Route path="/" element={<Home />} />
							<Route path="/resume" element={<Resume />} />
							<Route path="/portfolio" element={<Portfolio />} />
						</Routes>
					</div>
				</Content>
				<Footer style={{ textAlign: "center" }}>chaerin</Footer>
			</Router>
		</Layout>
	);
}

const Home = () => {
	return <div></div>;
};

const Resume = () => {
	return (
		<div>
			<h3></h3>
			<h3> : </h3>
			<h3></h3>
			<>
				<li></li>
				<li></li>
			</>
			<h3></h3>
			<>
				<li>  1</li>
			</>
		</div>
	);
};

const Portfolio = () => {
	return <></>;
};

export default App;
